
import { api } from "@/utils/api.js";
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: "login",
props: {},
components: {
XButton
},
data() {
return {
phone: "",
code: "",
new_password1: "",
new_password2: "",
countdown: 60
};
},
created() {
let that = this;
},
methods: {

getcode() {
let reg = /^1[0-9]{10,}$/; 
let _test = reg.test(this.phone);
if (_test) {
this.setcode();
} else {
this.$vux.toast.text("请输入正确的手机号码", "top");
}
},
async setcode() {
let that = this;
function settime(val) {
if (that.countdown == 0) {
that.countdown = 60;
that.flag = false;
} else {
that.countdown--;
setTimeout(function() {
settime();
}, 1000);
}
}

if (!this.flag) {
this.flag = true;
let timestamp = Date.parse(new Date()) / 1000;
let param = {
plat: 3,
tel: this.phone,
time: timestamp,
device_name: "web",
version: 1
};
let data = {
param: this.$encrypt(JSON.stringify(param))
};
const [err, res] = await api.register_sms(data);
if (err) {
this.$vux.toast.text(err.msg);
this.flag = false;
return;
}
this.$vux.toast.text(res.msg);
settime();
}
},

async checkInfo() {
let that = this;
let reg = /^1[0-9]{10,}$/; 
let _test = reg.test(this.phone);
if (!_test) {
this.$vux.toast.text("请输入正确的手机号");
return;
}
if (!this.code) {
this.$vux.toast.text("请输入正确的验证码");
return;
}
if (!this.new_password1 || !this.new_password2) {
this.$vux.toast.text("请输入密码");
return;
}
if (this.new_password1.length < 5 || this.new_password1.length > 12) {
this.$vux.toast.text("密码为6-12位数");
return;
}
if (this.new_password1 !== this.new_password2) {
this.$vux.toast.text("两次密码输入不一致");
return;
}
let data = {
tel: this.phone,
plat: 3,
pwd: this.$encrypt(this.new_password1),
review_pwd: this.$encrypt(this.new_password2),
code: this.code
};
this.$vux.loading.show({
text: "Loading"
});
const [err, res] = await api.register(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
this.$vux.toast.text(res.msg);
setTimeout(() => {
that.$router.push("/login");
}, 500);
}
}
};



